import React, { useEffect, useState } from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";

const API_KEY = "sXpGFDGZs0Dv1mmNFvYaGUvYwKX0PWIh";
const urlEndpointRandom = `https://api.giphy.com/v1/gifs/random?api_key=${API_KEY}&tag=`;
// const urlEndpoint = "https://api.giphy.com/v1/gifs/search?q="; /* PARA OBTENER SIEMPRE EL MISMO GIF */
// const giphyApiKey = "&api_key=sXpGFDGZs0Dv1mmNFvYaGUvYwKX0PWIh&limit=1"; /* PARA OBTENER SIEMPRE EL MISMO GIF */

const CharacterList = ({ characters }) => {
  const [gifUrl, setGifUrl] = useState("");
  const [isShown, setIsShown] = useState(false);
  const [loading, setLoading] = useState(false);
  const [counterClick, setCounterClick] = useState(0);
  const [selectedCharacter, setSelectedCharacter] = useState({});
  const [show, setShow] = useState(false);

  const handleCloseModal = () => setShow(false);
  const handleShowModal = () => setShow(true);

  const handleClickCloseGif = () => {
    setIsShown(false);
  };

  useEffect(() => {
    const fetchGifRandom = async () => {
      if (!selectedCharacter.name) return;
      setLoading(true);
      const res = await fetch(urlEndpointRandom + selectedCharacter.name);
      const data = await res.json();
      setGifUrl(data.data.images.original.url);
      setLoading(false);
      setIsShown(true);
    };

    fetchGifRandom();

    /* PARA OBTENER SIEMPRE EL MISMO GIF */
    /* const fetchSameGif = async () => {
      if (!selectedCharacter.name) return;
      setLoading(true);
      const res = await fetch(urlEndpoint + selectedCharacter.name + giphyApiKey);
      const data = await res.json();
      data.data.forEach(element => {
        setGifUrl(element.images.original.url);
      });
      setLoading(false);
      setIsShown(true);
    }; */

    // fetchSameGif();
  }, [counterClick, selectedCharacter]);

  return (
    <div className="container">
      <div className="row">
        <div className="containerTitle">
          <h3>
            Click on an image or character name to show a related gif from the
            character selected â¬‡
          </h3>
        </div>
        <div className="containerTitle">
          {selectedCharacter.name && isShown && (
            <Modal show={show} onHide={handleCloseModal}>
              <Modal.Header closeButton>
                <Modal.Title>Random <b>{selectedCharacter.name}</b> related
                    gif:</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <div className="gif">
                  <h5>Click gif image to close</h5>
                  {!loading ? (
                    <img
                      className="gif-container"
                      src={gifUrl}
                      alt={selectedCharacter.name}
                      onClick={handleClickCloseGif}
                    />
                  ) : (
                    <div className="gif-container">
                      <div
                        className="loader-container spinner-border text-primary"
                        role="status"
                      >
                        <span className="sr-only"></span>
                      </div>
                    </div>
                  )}
                </div>
              </Modal.Body>
              <Modal.Footer className="gif">
                <Button variant="primary" onClick={() => {
                    setCounterClick(counterClick + 1);
                  }}>
                  Try another gif!
                </Button>
              </Modal.Footer>
            </Modal>
          )}
        </div>
        <hr />
        {characters.map((character) => (
          <div key={character.id} className="col-lg-3 col-md-6 col-sm-12 mb-4">
            <div className="card" style={{ minWidth: "200px" }}>
              <div className="img-container card-body">
                <img
                  className="img-fluid character-img card-img-top"
                  src={character.image}
                  alt={character.name}
                  // onClick={() => setSelectedCharacter(character)}
                  onClick={() => {
                    setSelectedCharacter(character);
                    setCounterClick(counterClick + 1);
                    handleShowModal();
                  }}
                />
                <div className="overlay d-flex align-items-center justify-conten-center"></div>
              </div>
              <div className="card-body">
                <h4
                  className="card-title card-name"
                  // onClick={() => setSelectedCharacter(character)}
                  onClick={() => {
                    setSelectedCharacter(character);
                    setCounterClick(counterClick + 1);
                    handleShowModal();
                  }}
                >
                  {character.name}
                </h4>
                <div className="card-text">
                  {(() => {
                    switch (character.status) {
                      case "Alive":
                        return (
                          <div>
                            ðŸŸ¢ {character.status} - {character.species}
                          </div>
                        );
                      case "Dead":
                        return (
                          <div>
                            ðŸ”´ {character.status} - {character.species}
                          </div>
                        );
                      case "unknown":
                        return (
                          <div>
                            ðŸ”˜{" "}
                            {character.status.charAt(0).toUpperCase() +
                              character.status.slice(1)}{" "}
                            - {character.species}
                          </div>
                        );
                      default:
                        break;
                    }
                  })()}
                </div>
                <hr /> {/* LINEA */}
                <div className="card-text">
                  <b>Origin: </b>
                  {character.origin.name.charAt(0).toUpperCase() +
                    character.origin.name.slice(1)}
                </div>
                <div className="card-text">
                  <b>Gender: </b>
                  {character.gender}
                </div>
                <div className="card-text">
                  <b>Last known location: </b>
                  {character.location.name}
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default CharacterList;
