import React, { useEffect, useState } from "react";
import axios from "axios";
// components
// import Navbar from "./components/Navbar";
import CharacterList from "./components/CharacterList";
import Pagination from "./components/Pagination";

function App() {
  const [characters, setCharacters] = useState([]);
  const [info, setInfo] = useState({});
  const [page, setPage] = useState(1);
  const [query, setQuery] = useState("");
  const [error, setError] = useState(false);
  const url = "https://rickandmortyapi.com/api/character";
  
  const fetchCharacters = (url) => {
    if (query !== "") {
      axios
        .get(`https://rickandmortyapi.com/api/character/?name=${query}`)
        .then((data) => {
          setCharacters(data.data.results);
          setError(false);
        })
        .catch((error) => {
          console.error(error);
          setError(true);
        });
    } else {
      axios
        .get(url)
        .then((data) => {
          setCharacters(data.data.results);
          setInfo(data.data.info);
          setError(false);
        })
        .catch((error) => {
          console.error(error);
          setError(true);
        });
    }
  };

  const handleNextPage = () => {
    if (error) {
      setPage(1);
      setQuery("");
      setError(false);
      fetchCharacters(info.next);
    } else {
      setPage(page + 1);
      setError(false);
      fetchCharacters(info.next);
    }
    window.scrollTo(0, 0); // Desplaza el visor a un conjunto especÃ­fico de coodenadas en el documento.
  };

  const handlePreviousPage = () => {
    if (error) {
      setPage(1);
      setQuery("");
      setError(false);
      fetchCharacters(info.prev);
    } else {
      setPage(page - 1);
      setError(false);
      fetchCharacters(info.prev);
    }
    window.scrollTo(0, 0); // Desplaza el visor a un conjunto especÃ­fico de coodenadas en el documento.
  };

  const handleCLickButtonUp = () => {
    window.scrollTo(0, 0);
  };

  useEffect(() => {
    fetchCharacters(url);
  }, [query]);

  return (
    <>
      {/* NAVBAR */}
      {/* <Navbar brand="Rick and Morty App" /> */}
      <nav className="navbar navbar-light">
        <div className="container navbar-container">
          <div>
            <a href="http://www.Story-Train.com">
              <img
                className="navbar-img"
                src="https://www.pngkey.com/png/full/112-1128897_portal-gun-rick-and-morty-portal-gun-rick.png"
                alt="navbar-img"
              />
            </a>
            <a
              className="navbar-brand text-uppercase navbar-title"
              href="https://paulacsk.github.io/RickAndMorty-api-giphy/"
            >
              Rick and Morty API Giphy
            </a>
          </div>
          <div className="flex">
            <input
              className="bg-gray-50 border border-gray-300 text-sm w-full indent-2 p-2.5 outline-none focus:border-blue-500 focus:ring-2 rounded-tl rounded-bl"
              type="text"
              placeholder="Search character..."
              value={query}
              onChange={(event) => setQuery(event.target.value)}
            />
          </div>
        </div>
      </nav>
      <Pagination
        info={info}
        page={page}
        handleNextPage={handleNextPage}
        handlePreviousPage={handlePreviousPage}
      ></Pagination>
      {error ? (
        <div className="container">
          <div className="row">
            <div className="containerTitle">
              <h3>Aw geez... No character with that name, try another!</h3>
            </div>
            <img
              className="imgNotFound"
              alt="Not found"
              src="https://i.pinimg.com/originals/46/82/88/46828885c28a6bfa34dd4d6bd9986c8d.png"
            ></img>
          </div>
        </div>
      ) : (
        <CharacterList characters={characters} />
      )}

      <div className="containerButtonUp">
        <img
          className="imgButtonUp character-img"
          alt="img-Button-Up"
          src="https://images.fineartamerica.com/images/artworkimages/medium/3/2-rick-and-morty-diana-owen-transparent.png"
          onClick={handleCLickButtonUp}
        ></img>
        <p className="navbar-title">Up Morty!</p>
      </div>
      <Pagination
        info={info}
        page={page}
        handleNextPage={handleNextPage}
        handlePreviousPage={handlePreviousPage}
      ></Pagination>
    </>
  );
}

export default App;
